$color-index: (

        main_blue: #009CA2,
        main_grey: #31373E,
 
        main_red: #9A1E4F,
        light_aubergine: #77216F,
        mid_aubergine: #5E2750,
        dark_aubergine: #2C001E,
        latte: #8A5711,
        white: #ffffff,
        light_olive: #D3DAAE,
        olive: #4c6033,
        straw_berry: #9191AF,
        dark_grey: #454545,
        light_grey: #eeeeee,
        danger: #EF403D,
        warning: #FFCC00,
        success: #28A745,
        lime: #A6CE39,
        green: #48801F,
        orange: #F7941E,
        blue: #286FB2,
        red: #F00 ,
        yellow: #EACB01,
        alert_blue: #1F78D1
);


@function color-index($color-key) {
    @return map-get($color-index, $color-key);
}
