@import "../mixin/num_cols";
@import "../mixin/color_theme";
@import "../mixin/media_query";


// variables for flex tables
$table-header: #065479;
$table-header-border: #1565C0;
$table-border: #d9d9d9;
$row-bg: #f4f2f1;
// end flex tables

div {
  box-sizing: border-box;
}

.table-container {
  display: block;
 

  

  max-width: 100%;


  
}

.flag-icon {
  margin-right: 0.1em;
}

.flex-table {

  &.row{
    margin-left:0;
    margin-right:0;

  }

  display: flex;
  flex-flow: row wrap;
  border-left: solid 1px $table-border;
  transition: 0.5s;

  &:first-of-type {
    border-top: solid 1px $table-header-border;
    border-left: solid 1px $table-header-border;
  }

  &:first-of-type .flex-row {
    background: $table-header;
    color: white;
    border-color: $table-header-border;
  }

  &.row:nth-child(odd) .flex-row {
    background: $row-bg;
  }

  &:hover {
    background: #F5F5F5;
    transition: 500ms;
  }


    &.header {
      background-color: #000;
     // padding-top: 5rem;


  }
}

.flex-row {

  @include bp(medium){

    @include num_cols(1 , 20);

  }
  
  
  text-align: center;
  padding: 0.5em 0.5em;
  border-right: solid 1px $table-border;
  border-bottom: solid 1px $table-border;

  .inline-block{

    display: inline-block;
  }
}

.rowspan {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: center;
}

.column {
  display: flex;
  flex-flow: column wrap;
  width: 75%;
  padding: 0;

  .flex-row {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
    padding: 0;
    border: 0;
    border-bottom: solid 1px $table-border;
    


    &:hover {
      background: #F5F5F5;
      transition: 500ms;
    }
  }
}

.flex-cell {
  width: calc(100% / 3); //1px = border right
  text-align: center;
  padding: 0.5em 0.5em;
  border-right: solid 1px $table-border;
  //flex: 1 1 33.3%;
  &:last-child {
     border-right: 0;
  }
}




@media all and (max-width: 767px) {
  .flex-row {
    width: calc(100% / 3); //1px = border right

    &.first {
      width: 100%;
    }
  }

  .column {
    width: 100%;
  }
}

@media all and (max-width: 430px) {

  .flex-table {
    .flex-row {
      border-bottom: 0;
    }

    .flex-row:last-of-type {
      border-bottom: solid 1px $table-border;
    }
  }

  .header {
    .flex-row {
      border-bottom: solid 1px;
    }
  }

  .flex-row {
    width: 100%; //1px = border right

    &.first {
      width: 100%;
      border-bottom: solid 1px $table-border;
    }
  }

  .column {
    width: 100%;

    .flex-row {
      border-bottom: solid 1px $table-border;
    }
  }

  .flex-cell {
    width: 100%; //1px = border right
  }
}



@media print {



.flex-table {


  box-sizing: border-box;
  
  display: flex;

  .flex-row{

   padding: auto 0;
   flex: 1 1 0;
   width: 0;
  }
}

}